@import url(https://fonts.googleapis.com/css?family=Raleway);
.about {
  background-color: #e5f3fa;
  height: 48em;
  align-items: center;
  display: flex;
  flex-direction: column; }

button:focus {
  border: none;
  outline: none; }

.about {
  padding-top: 2em; }

.about h1 {
  color: #2d323d;
  -webkit-animation: fadeIn 0.2s linear forwards;
          animation: fadeIn 0.2s linear forwards;
  font-weight: bold;
  -webkit-animation: slideRight 0.4s linear forwards;
          animation: slideRight 0.4s linear forwards; }

.about-header-container {
  margin-bottom: 2em; }

.about-header-bar {
  border-bottom: 0.35em solid #2d323d;
  width: 8em;
  border-radius: 4%;
  display: flex;
  justify-content: center;
  -webkit-animation: slideLeft 0.4s linear forwards;
          animation: slideLeft 0.4s linear forwards; }

.about-icon {
  color: white;
  font-size: 40px; }

.hexagon {
  position: relative;
  width: 80px;
  height: 46.19px;
  background-color: #018fd3;
  margin: 23.09px 0;
  justify-content: center;
  display: flex;
  align-items: center; }

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent; }

.hexagon:before {
  bottom: 100%;
  border-bottom: 23.09px solid #018fd3; }

.hexagon:after {
  top: 100%;
  width: 0;
  border-top: 23.09px solid #018fd3; }

.about-article {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20em;
  min-height: 12em;
  color: #2E3441; }

h3,
p {
  margin-top: 0.3em;
  text-align: center; }

p {
  min-height: 3em;
  min-width: 5em; }

.about-article-container {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: baseline; }

.about-article-footer-container {
  display: flex;
  flex-direction: column;
  align-items: center; }

h2 {
  color: #018fd3; }

.about-article-footer-container-main {
  display: flex;
  flex-direction: column;
  color: #2E3441;
  align-content: flex-start;
  max-width: 40em;
  line-height: 2em;
  padding: 2em;
  border-radius: 0.2em;
  margin-bottom: 3em; }

.about-bio {
  text-align: start; }

.about-article-footer-container-skills li {
  min-width: 5em; }

.about-project-button,
.about-project-button:focus {
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
  color: #e5f3fa;
  width: 10em;
  height: 3em;
  background: #2E3441;
  border: 0.08em solid #018fd3;
  -webkit-transition: background-color 0.5s, border 0.5s;
  transition: background-color 0.5s, border 0.5s; }

.about-project-button:hover {
  background-color: #018fd3;
  border: 0.08em solid #018fd3;
  -webkit-transition: border 0.5s, background-color 0.5s;
  transition: border 0.5s, background-color 0.5s; }

* {
  font-family: "Raleway", sans-serif; }

main {
  background: -webkit-gradient(linear, left top, right bottom, color-stop(50%, #2f3441), color-stop(50%, #212531));
  background: -webkit-linear-gradient(left top, #2f3441 50%, #212531 50%);
  background: linear-gradient(to right bottom, #2f3441 50%, #212531 50%); }

.main--information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 3;
  position: relative;
  height: 100%; }

h1 {
  font-family: "Raleway", sans-serif;
  text-align: center;
  font-weight: lighter;
  font-size: 2.6em;
  color: #e5f3fa; }

.portfolio--container {
  position: relative; }

h1 span {
  color: #018fd3; }

.main--contact-btn {
  color: #e5f3fa;
  background: transparent;
  border: none;
  font-size: 3em;
  -webkit-transition: color 0.3s;
  transition: color 0.3s; }

.main--contact-container {
  display: flex;
  justify-content: space-around;
  width: 18em;
  margin: 2em; }

.main--contact-btn:hover {
  color: #018fd3;
  -webkit-transition: color 0.5s;
  transition: color 0.5s; }

.main--show-more-button,
.main--show-more-button:focus {
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
  color: #e5f3fa;
  width: 10em;
  height: 3em;
  background: transparent;
  border: 0.08em solid #e5f3fa;
  -webkit-transition: background-color 0.5s, border 0.5s;
  transition: background-color 0.5s, border 0.5s; }

.main--show-more-button:hover {
  background-color: #018fd3;
  border: 0.08em solid #018fd3;
  -webkit-transition: border 0.5s, background-color 0.5s;
  transition: border 0.5s, background-color 0.5s; }

.projects {
  height: 60em;
  background: #2E3441;
  align-items: center;
  display: flex;
  flex-direction: column; }

.project-header-container {
  margin-top: 2em; }

.project-header-bar {
  border-bottom: 0.35em solid #e5f3fa;
  width: 10em;
  border-radius: 4%;
  display: flex;
  justify-content: center;
  -webkit-animation: slideLeft 0.4s linear forwards;
          animation: slideLeft 0.4s linear forwards;
  margin-top: 0.4em; }

.project h1 {
  margin-bottom: 0.2em;
  color: #e5f3fa;
  -webkit-animation: fadeIn 0.2s linear forwards;
          animation: fadeIn 0.2s linear forwards;
  font-weight: bold;
  -webkit-animation: slideRight 0.4s linear forwards;
          animation: slideRight 0.4s linear forwards; }

nav {
  position: -webkit-sticky;
  position: sticky;
  z-index: 5;
  border-bottom: 3px groove #212531;
  background-color: #212531;
  padding: 0;
  -webkit-transition: padding 0.2s;
  transition: padding 0.2s;
  opacity: 0.98;
  top: 0; }

nav:hover {
  padding: 0.3em 0;
  -webkit-transition: padding 0.2s;
  transition: padding 0.2s; }

ul {
  list-style-type: none;
  margin-left: 15em;
  display: flex;
  width: 15em;
  justify-content: space-around;
  align-content: center; }

.fa-icon {
  margin: 0 0.6em; }

li {
  padding: 1em;
  margin: 0 2em;
  display: flex;
  font-family: "Raleway", sans-serif;
  color: #e5f3fa;
  -webkit-transition: color 0.5s;
  transition: color 0.5s; }

li:active {
  color: #018fd3;
  -webkit-transition: color 0.5s;
  transition: color 0.5s; }

li:hover {
  cursor: pointer;
  color: #018fd3;
  -webkit-transition: color 0.5s;
  transition: color 0.5s; }

* {
  margin: 0;
  padding: 0; }

#root {
  box-sizing: border-box; }

html,
body,
main,
.App {
  min-height: 100%; }

.portfolio-container {
  height: 100em; }

